import Vue from 'vue'
import moment from 'moment'
import util from '@/libs/util'
import {_} from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import XTable from 'c%/table'

export default{
	mixins: [ common,globals ],
	components: {
		XTable
	},
	props: {
		bids: {
			type: Array,
			default: []
		},
		yearMonth: {
			type: String,
			default: moment(new Date()).format('YYYY-MM')
		}
	},
	data () {
		return {
			data: []				
		}
	},
	watch: {	
		bids: function (val) {
			this.pageIndex = 1
			this.init_data()
		},
		yearMonth: function (val) {
			this.pageIndex = 1
			this.init_data()
		}
	},
	mounted () {
		this.init_data()
	},
	methods: {			
		init_data () {				
			this.showLoading()
			this.$rest(this.data_uri).get()
			.success(data=>{
				this.data = data.list
				this.loadFinished()
			})
		},
		loadFinished () {
			let data = this.data
			
			if(data&&data.length>0){
				data.push({
					column_total:true
				})
			}
			
			setTimeout(()=>{
				this.hideLoading()
			},300)
		},
		//获取列统计
		getTotal(h,field) {
			let data = this.data,
				result = 0
			if(data&&data.length>0){
				data.forEach(d=>{
					if(d[field]){				
						result += Number(d[field])							
					}
				})
				
				if(!_.isNumber(data[data.length-2][field])){
					result = parseFloat(result).toFixed(2)	
				}
			}
			return result
		},
		getProportion (row) {
			let data = this.data,
				total = 0,
				result = 0
			if(data&&data.length){
				data.forEach(d=>{
					if(d.subtotal){
						total += d.subtotal
					}
				})
				result = parseFloat((row.subtotal/total)*100).toFixed(2)
			}
			return result
		}
	},
	computed: {
		tableHeight () {
			let winHeight = window.innerHeight||document.innerHeight
			return winHeight - (46 + this.headerHeight) 
		},			
		columns () {
			let columns = []
			let first_column = {}
			
			let column_student_name = {
					title: '介绍人',
					key: 'student_name',
					width: 100,
					fixed: 'left',
					render: (h,params)=>{		
						if(params.row.column_total){
							return h('span','合计')
						}
						return h('span',params.row.referer_student.student_name)
					}
				},
				column_sno = {
					title: '学号',
					key: 'sno',
					width: 100,
					render: (h,params)=>{
						if(params.row.column_total){
							return '--'
						}
						return h('span',params.row.referer_student.sno)
					}
				},
				column_first_tel = {
					title: '电话',
					key: 'first_tel',
					width: 150,
					render: (h,params)=>{
						if(params.row.column_total){							
							return '--'
						}
						return params.row.referer_student.first_tel
					}
				},
				column_status = {
					title: '状态',
					key: 'status',
					width: 100,
					render: (h,params)=>{
						if(params.row.column_total){								
							return '--'
						}
						
						return h('span',params.row.status)
					}
				},
				column_subtotal = {
					title: '介绍人数',
					key: 'subtotal',
					width: 100,
					render: (h,params)=>{
						if(params.row.column_total){							
							return h('span',{'class':'text-danger'},this.getTotal(h,'subtotal'))
						}
						return params.row.subtotal
					}
				}					
				
			columns.push(
				column_student_name,
				column_sno,
				column_first_tel,
				column_status,
				column_subtotal
			)
			
			return columns
		},
		data_uri () {
			let start_date = this.yearMonth+'-01',
				end_date = this.yearMonth+'-'+moment(this.yearMonth).daysInMonth(),
				bids = ''

				if(this.bids.length){
					bids = util.sprintf('&bid=[In,%s]',this.bids.join(',')) 
				}
			let uri = util.sprintf('report_customers?group=referer_sid&start_date=%s&end_date=%s%s',start_date,end_date,bids)
									
			return uri
		}
	}
}



