
import report from '@/libs/report.mixin'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import SelectBranch from 'c%/SelectBranch.vue'
import TableReferer from './customer/table-referer.vue'
import TableAnalysis from './customer/table-analysis.vue'
import TableSalesFunnel from './customer/table-sales-funnel.vue'
import TablePercentConversion from './customer/table-percent-conversion.vue'
export default{
mixins: [report],
components: {
Datetime,
PopupPicker,
SelectBranch,
TableReferer,
TableAnalysis,
TableSalesFunnel,
TablePercentConversion
},
data () {
return {
popupShow: false,
filter: ['from_did'],
filterList:[
{ name:'按来源统计', value: 'from_did' },
{ name:'按意向统计', value: 'intention_level' },
{ name:'销售漏斗', value: '1' },
{ name:'转化率分析', value: '2' },
{ name:'转介绍统计', value: '3' }
]
}
}
}




