import Vue from 'vue'
import moment from 'moment'
import util from '@/libs/util'
import {_} from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import XTable from 'c%/table'

export default{
	mixins: [ common,globals ],
	components: {
		XTable
	},
	props: {	
		bids: {
			type: Array,
			default: []
		},
		yearMonth: {
			type: String,
			default: moment(new Date()).format('YYYY-MM')
		}
	},
	data () {
		return {
			data: [],
			columns: []
		}
	},
	watch: {	
		bids: function (val) {
			this.pageIndex = 1
			this.init_data()
		},
		yearMonth: function (val) {
			this.pageIndex = 1
			this.init_data()
		}
	},
	mounted () {
		this.formatColumn()
		this.init_data()
	},
	methods: {			
		init_data () {				
			this.showLoading()
			this.$rest(this.data_uri).get()
			.success(data=>{
				this.data = this.formatData(data)
				this.loadFinished()
			})
		},
		loadFinished () {
			let data = this.data
			
			if(data&&data.length>0){
				data.push({
					column_total:true
				})
			}
			
			setTimeout(()=>{
				this.hideLoading()
			},300)
		},
		//获取列统计
		getTotal(h,field) {
			let data = this.data,
				result = 0
			if(data&&data.length>0){
				data.forEach(d=>{
					if(d[field]){				
						result += Number(d[field])							
					}
				})
				
				if(!_.isNumber(data[data.length-2][field])){
					result = parseFloat(result).toFixed(2)	
				}
			}
			return result
		},
		formatColumn () {
			let columns = []
			columns.push({
				title: '校区',
				key: 'bid',
				fixed: 'left',
				width: 100,
				render: (h,params)=>{
					return this.$filter('branch_name')(params.row.bid)
				}
			})
			this.cs_list.forEach(item => {
				let key = item.did + item.title
				columns.push({
					title: item.title,
					key: key,						
					width: 100
				})
			})
			columns.push({
				title: '未指定',
				key: '0未指定',
				width: 100					
			})
			columns.push({
				title: '总计',
				key: 'total', 
				fixed: 'right',
				width: 70
			})
			this.columns = columns
		},
		formatData (data) {
			let group = [],copy = []
			let cs_list = this.cs_list				
			data.list.forEach(i => {
				let key = ''
				let branch_obj = group.find(g => g.bid == i.bid)
				let cs_obj = cs_list.find(l => l.did == i.customer_status_did)
				if(cs_obj) {
					key = i.customer_status_did + cs_obj.title
				}else{
					key = i.customer_status_did + '未指定'
				}
				if(branch_obj) {
					branch_obj[key] = i.subtotal
					branch_obj.total += i.subtotal
				}else{
					let obj = {}
					cs_list.forEach(l => {
						obj[l.did + l.title] = 0
					})
					obj.bid = i.bid
					obj.total = 0
					obj['0未指定'] = 0
					obj[key] = i.subtotal
					obj.total += i.subtotal
					group.push(obj)
				}
			})
			return group
		},
	},
	computed: {
		tableHeight () {
			let winHeight = window.innerHeight||document.innerHeight
			return winHeight - (46 + this.headerHeight) 
		},			
		cs_list() {
			return this.$store.state.gvars.dicts['customer_status']
		},			
		data_uri () {
			let start_date = this.yearMonth+'-01',
				end_date = this.yearMonth+'-'+moment(this.yearMonth).daysInMonth(),
				bids = ''

				if(this.bids.length){
					bids = util.sprintf('&bid=[In,%s]',this.bids.join(',')) 
				}
			let	uri = util.sprintf('report_customers?group=bid,customer_status_did&start_date=%s&end_date=%s%s',start_date,end_date,bids)
									
			return uri
		}
	}
}



