import Vue from 'vue'
import moment from 'moment'
import util from '@/libs/util'
import {_} from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import XTable from 'c%/table'

export default{
	mixins: [ common,globals ],
	components: {
		XTable
	},
	props: {
		bids: {
			type: Array,
			default: []
		},
		yearMonth: {
			type: String,
			default: moment(new Date()).format('YYYY-MM')
		}
	},
	data () {
		return {
			data: []				
		}
	},
	watch: {	
		bids: function (val) {
			this.pageIndex = 1
			this.init_data()
		},
		yearMonth: function (val) {
			this.pageIndex = 1
			this.init_data()
		}
	},
	mounted () {
		this.init_data()
	},
	methods: {			
		init_data () {				
			this.showLoading()
			this.$rest(this.data_uri).get()
			.success(data=>{
				this.data = data.list
				this.loadFinished()
			})
		},
		loadFinished () {
			let data = this.data
			
			if(data&&data.length>0){
				data.push({
					column_total:true
				})
			}
			
			setTimeout(()=>{
				this.hideLoading()
			},300)
		},
		//获取列统计
		getTotal(h,field) {
			let data = this.data,
				result = 0
			if(data&&data.length>0){
				data.forEach(d=>{
					if(d[field]){				
						result += Number(d[field])							
					}
				})
				
				if(!_.isNumber(data[data.length-2][field])){
					result = parseFloat(result).toFixed(2)	
				}
			}
			return result
		},
		getProportion (row) {
			let data = this.data,
				total = 0,
				result = 0
			if(data&&data.length){
				data.forEach(d=>{
					if(d.subtotal){
						total += d.subtotal
					}
				})
				result = parseFloat((row.subtotal/total)*100).toFixed(2)
			}
			return result
		}
	},
	computed: {
		tableHeight () {
			let winHeight = window.innerHeight||document.innerHeight
			return winHeight - (46 + this.headerHeight) 
		},			
		columns () {
			let columns = []
			let first_column = {}
			
			let column_old_value= {
					title: '客户状态',
					key: 'old_value',
					width: 100,
					fixed: 'left',
					render: (h,params)=>{		
						if(params.row.column_total){
							return h('span','合计')
						}
						return h('span',this.labelDicts(params.row.old_value,'customer_status'))
					}
				},
				column_count_follow_up = {
					title: '已跟进人数',
					key: 'count_follow_up',
					width: 120,
					render: (h,params)=>{
						if(params.row.column_total){
							return h('span',{'class':'text-danger'},this.getTotal(h,'count_follow_up'))
						}
						return h('span',params.row.count_follow_up)
					}
				},
				column_count_advance = {
					title: '进阶人数',
					key: 'count_advance',
					width: 100,
					render: (h,params)=>{
						if(params.row.column_total){							
							return h('span',{'class':'text-danger'},this.getTotal(h,'count_advance'))
						}
						return params.row.count_advance
					}
				},
				column_advance_rate = {
					title: '进阶率',
					key: 'advance_rate',
					width: 100,
					render: (h,params)=>{
						if(params.row.column_total){								
							return '--'
						}
						
						return h('span',(params.row.advance_rate*100).toFixed(2)+'%')
					}
				},
				column_count_transfer = {
					title: '转化人数',
					key: 'count_transfer',
					width: 100,
					render: (h,params)=>{
						if(params.row.column_total){							
							return h('span',{'class':'text-danger'},this.getTotal(h,'count_transfer'))
						}
						return params.row.count_transfer
					}
				},
				column_conversion_rate = {
					title: '转化率',
					key: 'conversion_rate',
					width: 100,
					render: (h,params)=>{
						if(params.row.column_total){								
							return '--'
						}
						
						return h('span',(params.row.conversion_rate*100).toFixed(2)+'%')
					}
				}
				
			columns.push(
				column_old_value,
				column_count_follow_up,
				column_count_advance,
				column_advance_rate,
				column_count_transfer,
				column_conversion_rate
			)
			
			return columns
		},
		data_uri () {
			let start_date = this.yearMonth+'-01',
				end_date = this.yearMonth+'-'+moment(this.yearMonth).daysInMonth(),
				bids = ''

				if(this.bids.length){
					bids = util.sprintf('&bid=%s',this.bids.join(',')) 
				}
			let	uri = util.sprintf('report_customers/conversion_rate?date_period=%s,%s%s',start_date,end_date,bids)
									
			return uri
		}
	}
}



