	import Vue from 'vue'
	import moment from 'moment'
	import util from '@/libs/util'
	import {_} from '@/libs/util'
	import common from '@/libs/common.mixin'
	import globals from '@/libs/globals.mixin'
	import XTable from 'c%/table'
	
	export default{
		mixins: [ common,globals ],
		components: {
			XTable
		},
		props: {
			bids: {
				type: Array,
				default: []
			},
			group: {
				type: String,
				default: 'from_did'
			},
			yearMonth: {
				type: String,
				default: moment(new Date()).format('YYYY-MM')
			}
		},
		data () {
			return {
				data: []				
			}
		},
		watch: {
			bids: function (val) {
				this.pageIndex = 1
				this.init_data()
			},
			group: function (val) {				
				this.pageIndex = 1
				this.init_data()				
			},
			yearMonth: function (val) {
				this.pageIndex = 1
				this.init_data()
			}
		},
		mounted () {
			this.init_data()
		},
		methods: {			
			init_data () {				
				this.showLoading()
				if(this.group=='from_did'||this.group=='intention_level'){
					this.$rest(this.data_uri).get()
					.success(data=>{
						this.data = data.list
						this.loadFinished()
					})
				}
			},
			loadFinished () {
				let data = this.data
				
				if(data&&data.length>0){
					data.push({
						column_total:true
					})
				}
				
				setTimeout(()=>{
					this.hideLoading()
				},300)
			},
			//获取列统计
			getTotal(h,field) {
				let data = this.data,
					result = 0
				if(data&&data.length>0){
					data.forEach(d=>{
						if(d[field]){				
							result += Number(d[field])							
						}
					})
					
					if(!_.isNumber(data[data.length-2][field])){
						result = parseFloat(result).toFixed(2)	
					}
				}
				return result
			},
			getProportion (row) {
				let data = this.data,
					total = 0,
					result = 0
				if(data&&data.length){
					data.forEach(d=>{
						if(d.subtotal){
							total += d.subtotal
						}
					})
					result = parseFloat((row.subtotal/total)*100).toFixed(2)
				}
				return result
			}
		},
		computed: {
			tableHeight () {
				let winHeight = window.innerHeight||document.innerHeight
				return winHeight - (46 + this.headerHeight) 
			},			
			columns () {
				let columns = []
				let first_column = {}
				if(this.group=='from_did'){
					first_column = {
						title: '招生来源',
						key: 'from_did',
						width: 120,
						fixed: 'left',
                        render: (h,params)=>{
                        	if(params.row.column_total){
                        		return '合计'
                        	}                        	
							return h('span',this.labelDicts(params.row.from_did,'from'))                        	
                        }
					}
				}else {				
					first_column = {
						title: '意向级别',
						key: 'intention_level',
						width: 120,
						fixed: 'left',
						render: (h,params)=>{
							if(params.row.column_total){
                        		return '合计'
                        	}
							
							return h('span',params.row.intention_level)
						}
					}
				}
				let column_subtotal = {
						title: '客户数量',
						key: 'subtotal',
						width: 100,
						render: (h,params)=>{
							if(params.row.column_total){
								return h('span',{'class':'text-danger'},this.getTotal(h,'subtotal'))
							}
							return params.row.subtotal
						}
					},
					column_proportion = {
						title: '比例',
						key: 'proportion',
						width: 100,
						render: (h,params)=>{
							if(params.row.column_total){
								return '--'
							}
							return this.getProportion(params.row)
						}
					},
					column_transfer_nums = {
						title: '转化成功',
						key: 'transfer_nums',
						width: 100,
						render: (h,params)=>{
							if(params.row.column_total){							
								return h('span',{'class':'text-danger'},this.getTotal(h,'transfer_nums'))
							}
							return params.row.transfer_nums
						}
					},
					column_transfer_rate = {
						title: '转化率',
						key: 'transfer_rate',
						width: 100,
						render: (h,params)=>{
							if(params.row.column_total){
								let total = this.getTotal(h,'subtotal'),
									transfer = this.getTotal(h,'transfer_nums'), 
									result = parseFloat((transfer / total) * 100).toFixed(2)								
								
								return h('span',{'class':'text-danger'},result+'%')
							}
							
							let row = params.row,
								result = parseFloat((row.transfer_nums/row.subtotal)*100).toFixed(2)
							
							return h('span',result+'%')
						}
					}
					
				columns.push(first_column,column_subtotal,column_proportion,column_transfer_nums,column_transfer_rate)
				
				return columns
			},
			data_uri () {
				let group = this.group,
					start_date = this.yearMonth+'-01',
					end_date = this.yearMonth+'-'+moment(this.yearMonth).daysInMonth(),
					base_uri = 'report_customers',					
					bids = ''

					if(this.bids.length){
						bids = util.sprintf('&bid=[In,%s]',this.bids.join(',')) 
					}
				let uri = util.sprintf('%s?group=%s&start_date=%s&end_date=%s%s',base_uri,group,start_date,end_date,bids)
				return uri
			}
		}
	}



